	module.exports = {"data":{"challengeNode":{"title":"Problem 154: Exploring Pascal's pyramid","description":["A triangular pyramid is constructed using spherical balls so that each ball rests on exactly three balls of the next lower level.","","Then, we calculate the number of paths leading from the apex to each position:","A path starts at the apex and progresses downwards to any of the three spheres directly below the current position.","Consequently, the number of paths to reach a certain position is the sum of the numbers immediately above it (depending on the position, there are up to three numbers above it).","The result is Pascal's pyramid and the numbers at each level n are the coefficients of the trinomial expansion ","(x + y + z)n.","How many coefficients in the expansion of (x + y + z)200000 are multiples of 1012?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-154-exploring-pascals-pyramid","blockName":"Project Euler","tests":[{"text":"<code>euler154()</code> should return 479742450.","testString":"assert.strictEqual(euler154(), 479742450, '<code>euler154()</code> should return 479742450.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler154() {\n  // Good luck!\n  return true;\n}\n\neuler154();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-155-counting-capacitor-circuits","id":"5900f4071000cf542c50ff19"},"slug":"/coding-interview-prep/project-euler/problem-154-exploring-pascals-pyramid"}}

